<template>
  <el-row :gutter="20">
    <el-col :span="3">
      <el-card>

        <div slot="header" class="clearfix">
          <span> </span>
        </div>
        <el-menu default-active="0" active-text-color="#E2A00B">
          <el-menu-item v-for="(channel, index) in channels" :key="channel.key" :index="index">
            <el-link :underline="false" @click.native="channelClick(channel.key)">{{ channel.name }}</el-link>
          </el-menu-item>
        </el-menu>

        <!--          <el-tag v-if="item.shared" :key="item.key" type="info" effect="dark" size="mini">-->
        <!--            <i class="el-icon-share"></i></el-tag>-->
        <!--          <el-tag v-if="item.encrypt" :key="item.key" type="info" effect="dark" size="mini"><i class="el-icon-key"></i>-->
        <!--          </el-tag>-->


      </el-card>


    </el-col>
    <el-col :span="6">
      <el-table
          :data="mediacontents"
          :default-sort="{prop:'title', order:'descending'}"
          ref="singleTable"
          highlight-current-row
          @current-change="selectMck"
          border
          style="width: 100%;">
        <el-table-column
            prop="poster_url"
            label="">
          <template slot-scope="scope">
            <el-image :src="scope.row.poster_url" style="width: 100%; height: 100%;"
                      fit="cover"/>
          </template>
        </el-table-column>
        <el-table-column
            prop="title"
            label=""
            sortable>
        </el-table-column>
        <el-table-column
            prop="media_content_key"
            label=""
        >
        </el-table-column>
      </el-table>
      <el-pagination
          background
          layout="prev, pager, next"
          :total="mediaContentsLen">
      </el-pagination>
    </el-col>
    <el-col :span="15">
      <el-row :gutter="10">
        <el-col :span="12">
          <span></span>
          <el-tag type="info" v-if="selectedChannel !=='' " :closable="false">{{ selectedChannel }}</el-tag>
        </el-col>
        <el-col :span="12">
          <span></span>
          <el-tag type="info" v-if="selectedMck !=='' " :closable="false">{{ selectedMck }}</el-tag>
        </el-col>
      </el-row>
      <el-tabs type="border-card">
        <el-tab-pane label="Jwt ">
          <el-form ref="jwtForm" label-position="left" label-width="100px">
            <el-form-item label="">
              <el-input-number v-model="jwt.expt" :step="60" :min="0" :max="3600"
                               label="" style="width: 100%;"></el-input-number>
            </el-form-item>

            <el-form-item label="">
              <el-switch v-model="jwt.intro"
                         active-text=""
                         inactive-text=""
              ></el-switch>
            </el-form-item>
            <el-form-item v-if="jwt.intro" label=" ">
              <el-input v-model="jwt.intro_mck" style="width: 100%;" :disabled="jwt.intro"></el-input>
            </el-form-item>
            <el-form-item label="">
              <el-switch v-model="jwt.outro"
                         active-text=""
                         inactive-text=""

              ></el-switch>
            </el-form-item>
            <el-form-item v-if="jwt.outro" label=" ">
              <el-input v-model="jwt.outro_mck" style="width: 100%;" :disabled="jwt.outro"></el-input>
            </el-form-item>
            <el-form-item label=" ">
              <el-switch v-model="jwt.scroll_event"
                         active-text=""
                         inactive-text=""
              ></el-switch>
            </el-form-item>
            <el-form-item label="Seek ">
              <el-switch v-model="jwt.seek"
                         active-text=""
                         inactive-text=""

              ></el-switch>
            </el-form-item>
            <el-form-item v-if="!jwt.seek" label="Seek  ">
              <el-input-number v-model="jwt.seekable_end" :step="1" :min="-1"
                               style="width: 100%;" :disabled="jwt.seek"></el-input-number>
            </el-form-item>
            <el-form-item label=" ">
              <el-switch v-model="jwt.disable_playrate"
                         active-text=""
                         inactive-text=""

              ></el-switch>
            </el-form-item>
            <el-form-item label=" ">
              <el-switch v-model="jwt.disable_nscreen"
                         active-text=""
                         inactive-text=""

              ></el-switch>
            </el-form-item>
            <el-form-item label=" ( )">
              <el-input-number v-model="jwt.start_time" :step="1" :min="-1"
                               style="width: 50%;"></el-input-number>
              <el-input-number v-model="jwt.end_time" :step="1" :min="-1"
                               style="width: 50%;"></el-input-number>
            </el-form-item>

            <el-form-item label="  ">
              <el-switch v-model="jwt.enable_video_water_marking"
                         active-text=""
                         inactive-text=""

              ></el-switch>
            </el-form-item>

            <el-form-item label="   ">
              <el-input v-model="jwt.code_kind" style="width: 100%;"></el-input>
            </el-form-item>
            <el-form-item label="  ">
              <el-input-number v-model="jwt.alpha" :step="1" :min="0" :max="255"
                               style="width: 50%;"></el-input-number>
            </el-form-item>
            <el-form-item label="   ">
              <el-input-number v-model="jwt.font_size" :step="1" :min="1" :max="255"
                               style="width: 50%;">
              </el-input-number>
            </el-form-item>
            <el-form-item label="   ">
              <el-color-picker v-model="jwt.font_color"></el-color-picker>
            </el-form-item>
            <el-form-item label="  / ">
              <el-input-number v-model="jwt.show_time" :step="1" :min="-1"
                               style="width: 50%;"></el-input-number>
              <el-input-number v-model="jwt.hide_time" :step="1" :min="-1"
                               style="width: 50%;"></el-input-number>
            </el-form-item>
            <el-form-item label="  HTML5 ">
              <el-switch v-model="jwt.enable_html5_player"
                         active-text=""
                         inactive-text=""

              ></el-switch>
            </el-form-item>
            <el-button type="primary" v-on:click="jwtOk"></el-button>
          </el-form>
        </el-tab-pane>
        <el-tab-pane label="Play Callback">
          <el-form ref="playcallbackform" label-position="left" label-width="200px">
            <el-form-item label="">
              <el-switch v-model="playcallback.result"
                         active-text=""
                         active-value="1"
                         inactive-text=""
                         inactive-value="0"
              ></el-switch>
            </el-form-item>
            <el-form-item v-if="playcallback.result == 0" label="  ">
              <el-input v-model="playcallback.message" style="width: 100%;"></el-input>
            </el-form-item>
            <el-form-item label="  ">
              <el-date-picker
                  v-model="playcallback.expiration_date"
                  type="datetime"
                  placeholder="  " style="width: 80%;">
              </el-date-picker>
            </el-form-item>
            <el-form-item label="  ">
              <el-input-number v-model="playcallback.expiration_playtime" :step="1" :min="0"
                               style="width: 100%;"></el-input-number>
            </el-form-item>
            <el-form-item label="  ">
              <el-switch v-model="playcallback.vmcheck"
                         active-text=""
                         active-value="1"
                         inactive-text=""
                         inactive-value="0"
              ></el-switch>
            </el-form-item>
            <el-form-item label="   ">
              <el-input-number v-model="playcallback.start_time" :step="1" :min="-1"
                               style="width: 50%;"></el-input-number>
              <el-input-number v-model="playcallback.end_time" :step="1" :min="-1"
                               style="width: 50%;"></el-input-number>
            </el-form-item>
            <el-form-item label="TV OUT ">
              <el-switch v-model="playcallback.disable_tvout"
                         active-text=""
                         active-value="1"
                         inactive-text=""
                         inactive-value="0"
              ></el-switch>
            </el-form-item>
            <el-form-item label=" ">
              <el-switch v-model="playcallback.content_expired"
                         active-text=""
                         active-value="1"
                         inactive-text=""
                         inactive-value="0"
              ></el-switch>
            </el-form-item>
            <el-button type="primary" v-on:click="playCallbackOk"></el-button>
          </el-form>
        </el-tab-pane>
        <el-tab-pane label="DRM Callback">
          <el-form ref="drmcallbackform" label-position="left" label-width="200px">
            <el-form-item label="">
              <el-switch v-model="drmcallback.result"
                         active-text=""
                         active-value="1"
                         inactive-text=""
                         inactive-value="0"
              ></el-switch>
            </el-form-item>
            <el-form-item v-if="drmcallback.result == 0" label="  ">
              <el-input v-model="drmcallback.message" style="width: 100%;"></el-input>
            </el-form-item>
            <el-form-item label="   ">
              <el-date-picker
                  v-model="drmcallback.expiration_date"
                  type="datetime"
                  placeholder="  " style="width: 80%;">
              </el-date-picker>
            </el-form-item>
            <el-form-item label="    ">
              <el-input-number v-model="drmcallback.expiration_count" :step="1" :min="0"
                               style="width: 100%;"></el-input-number>
            </el-form-item>
            <el-form-item label="    ">
              <el-input-number v-model="drmcallback.expiration_playtime" :step="1" :min="0"
                               style="width: 100%;"></el-input-number>
            </el-form-item>
            <el-form-item label="      ">
              <el-switch v-model="drmcallback.vmcheck"
                         active-text=""
                         active-value="1"
                         inactive-text=""
                         inactive-value="0"
              ></el-switch>
            </el-form-item>

            <el-form-item label="     ">
              <el-switch v-model="drmcallback.vmcheck"
                         active-text=""
                         active-value="1"
                         inactive-text=""
                         inactive-value="0"
              ></el-switch>
            </el-form-item>

            <el-form-item label="      ">
              <el-switch v-model="drmcallback.check_abuse"
                         active-text=""
                         active-value="1"
                         inactive-text=""
                         inactive-value="0"
              ></el-switch>
            </el-form-item>

            <el-form-item label="   ">
              <el-switch v-model="drmcallback.bookmark_download"
                         active-text="  "
                         active-value="1"
                         inactive-text=""
                         inactive-value="0"
              ></el-switch>
            </el-form-item>
            <el-form-item label="    ">
              <el-switch v-model="drmcallback.bookmark_readonly"
                         active-text=""
                         active-value="1"
                         inactive-text=""
                         inactive-value="0"
              ></el-switch>
            </el-form-item>

            <el-form-item label="  ">
              <el-switch v-model="drmcallback.content_delete"
                         active-text=""
                         active-value="1"
                         inactive-text=""
                         inactive-value="0"
              ></el-switch>
            </el-form-item>
            <el-form-item label="     ">
              <el-date-picker
                  v-model="drmcallback.check_expiration_date"
                  type="datetime"
                  placeholder="  " style="width: 80%;">
              </el-date-picker>
            </el-form-item>

            <el-form-item label="  ">
              <el-switch v-model="drmcallback.vmcheck"
                         active-text=""
                         active-value="1"
                         inactive-text=""
                         inactive-value="0"
              ></el-switch>
            </el-form-item>
            <el-form-item label="   ">
              <el-input-number v-model="drmcallback.start_time" :step="1" :min="-1"
                               style="width: 50%;"></el-input-number>
              <el-input-number v-model="drmcallback.end_time" :step="1" :min="-1"
                               style="width: 50%;"></el-input-number>
            </el-form-item>
            <el-form-item label="TV OUT ">
              <el-switch v-model="drmcallback.disable_tvout"
                         active-text=""
                         active-value="1"
                         inactive-text=""
                         inactive-value="0"
              ></el-switch>
            </el-form-item>
            <el-form-item label=" ">
              <el-switch v-model="drmcallback.content_expired"
                         active-text=""
                         active-value="1"
                         inactive-text=""
                         inactive-value="0"
              ></el-switch>
            </el-form-item>
            <el-form-item label="   ">
              <el-switch v-model="drmcallback.content_expire_reset"
                         active-text=""
                         active-value="1"
                         inactive-text=""
                         inactive-value="0"
              ></el-switch>
            </el-form-item>
            <el-button type="primary" v-on:click="drmCallbackOk"></el-button>
          </el-form>
        </el-tab-pane>
      </el-tabs>
    </el-col>
  </el-row>
</template>

<script>
import KollusService from '../../service/kollus.service';

export default {
  name: "channels",
  data() {
    return {
      channels: [],
      selectedChannel: '',
      selectedMck: '',
      mediaContentsLen: 0,
      mediacontents: [],
      jwt: {
        expt: 300,
        intro: false,
        intro_mck: '',
        outro: false,
        outro_mck: '',
        scroll_event: false,
        seek: true,
        seekable_end: -1,
        disable_playrate: false,
        disable_nscreen: false,
        start_time: -1,
        end_time: -1,
        enable_video_water_marking: false,
        code_kind: 'client_user_id',
        alpha: 200,
        font_size: 7,
        font_color: '#FFFFFF',
        show_time: 1,
        hide_time: 60,
        enable_html5_player: false
      },
      playcallback: {
        expiration_date: 0,
        result: 1,
        message: '',
        vmcheck: 0,
        start_time: 0,
        end_time: 0,
        disable_tvout: 0,
        expiration_playtime: 0,
        content_expired: 0
      },
      drmcallback: {
        expiration_date: 0,
        expiration_count: 0,
        expiration_playtime: 0,
        expiration_playtime_type: 0,
        result: 1,
        message: '',
        expiration_refresh_popup: 0,
        vmcheck: 0,
        check_abuse: 0,
        bookmark_download: 0,
        bookmark_readonly: 0,
        content_delete: 0,
        check_expiration_date: 0,
        content_expired: 0,
        content_expire_reset: 0
      }

    }
  },
  created() {
    KollusService.getChannels().then(response => {
      this.channels = response.data
      this.channelClick(response.data[0].key);
    });
  },
  methods: {
    channelClick(channel_key) {
      this.selectedChannel = channel_key;
      this.selectedMck = '';
      KollusService.getContentsByChannel(channel_key).then(response => {
        this.mediacontents = response.data
      });
      KollusService.getPolicy(channel_key).then(response => {
        this.jwt = response.data.jwt;
        this.playcallback = response.data.playcallback;
        this.drmcallback = response.data.drmcallback;
      });
    },
    selectMck(row) {
      this.selectedMck = row.media_content_key;
      KollusService.getPolicyWithMck(this.selectedChannel, this.selectedMck).then(
          response => {
            this.jwt = response.data.jwt;
            this.playcallback = response.data.playcallback;
            this.drmcallback = response.data.drmcallback;
          }
      );

    },
    jwtOk() {
      if (this.selectedMck !== '') {
        KollusService.savePolicyWithMck('jwt', this.jwt, this.selectedChannel, this.selectedMck);
      } else {

        KollusService.savePolicy('jwt', this.jwt, this.selectedChannel);
      }
    },
    playCallbackOk() {
      if (this.selectedMck !== '') {
        KollusService.savePolicyWithMck('playcallback', this.playcallback, this.selectedChannel, this.selectedMck);
      } else {
        KollusService.savePolicy('playcallback', this.playcallback, this.selectedChannel);
      }
    },
    drmCallbackOk() {
      if (this.selectedMck !== '') {
        KollusService.savePolicyWithMck('drmcallback', this.drmcallback, this.selectedChannel, this.selectedMck);
      } else {
        KollusService.savePolicy('drmcallback', this.drmcallback, this.selectedChannel);
      }
    },

  }
}
</script>

<style>

.el-table__row.current-row {
  background-color: #ffdb1f;
}
</style>
